.gallery__img {
  max-width: 100%;
  height: 100%;
  margin: auto;
  animation-name: changeGallery;
  animation-duration: 15s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
  display: block;
  position: absolute;
  left: 0;
  right: 0;
}

